    //         height: $large-height;
    //     }

    //     &:after {
    //         width: $large-margin;
    //         background-position: right -2*$large-height;
    //         right: 0;
    //         height: $large-height;
    //     }
    // }

    &.size-smallest {
        width: $item-size-smallest + $margin;
        height: $item-size-smallest + $margin;
        
        .text {
            font-size: smaller;
        }
    }

    &.size-medium, &.size-auto {
        width: $item-size-medium + 2 * $margin;
        height: $item-size-medium + 2 * $margin;
    }

   

