@import 'components/styles/variables.scss';

.adventurer-panel {
  padding-right: 8px;

  .info {
    .name {
      font-weight: bold;
    }

    .flavor {
      font-size: smaller;
      font-style: italic;
      color: $color-text-medium;

      .portrait {
        float: right;
        margin-left: 1rem;
        width: 25%;
      }
    }

    .health {
      clear: both;
    }
  }

  .right {
    padding-top: 16px;
  }

  &.horizontal {
    display: flex;

    .right {
      padding: 16px;
    }
  }

  @media only screen and (max-width: $max-width-small) {
    &.horizontal {
      /** small screen horizontal layout */
      flex-direction: column;

      .left {
        padding-right: 16px;
      }
    }

    .right {
      padding-left: 16px;
    }
  }

}
