@import 'components/styles/variables.scss';

.room-list {
    overflow: auto;
    flex: 1;
    margin: 8px;

    .adventurer-details {
        border: 1px solid grey;
        padding: 8px;
        top: -4px;
        position: relative;
    }
    
    .room {
        @include border-grey-sm;
        margin-bottom: 4px;
        padding: 4px;
        height: $item-size-medium + 10px;
        box-sizing: border-box;
        display: flex;
    
        &.expanded {
            background-color: $color-secondary;
            color: $color-text-lighter;
        }
        
        .adventurer-icon {
            margin-right: 6px;
        }

        .on-a-quest {
            color: $color-text-darker;
        }
        
        .boot {
            float: right
        }

        .rename {
            margin-left: auto;
            color: $color-link;
			cursor: var(--app-cursor-pointer);

            &:hover {
                text-decoration: underline;
            }
        }
    }
}
